<template>
  <Banner msg="TypeType"/>

  <Timer :second="second"/>

  <ParagraphBox :paragraph="paragraph"/>

  <TypingBox :paragraph="paragraph" :second="second" :timer="timer" v-on:start-timer="startTimer" v-on:fetch-paragraph="fetchParagraph" v-on:reset-timer="resetTimer"/>

  <Footer/>
</template>

<script>
import Banner from './components/Banner.vue'
import ParagraphBox from './components/ParagraphBox.vue'
import TypingBox from './components/TypingBox.vue'
import Timer from './components/Timer.vue'
import Footer from './components/Footer.vue'

export default {
  name: 'App',
  components: {
    Banner,
    ParagraphBox,
    TypingBox,
    Footer,
    Timer
  },
  data() {
    return {
      paragraph: '',
      paragraph_count: 1,
      second: 60,
      timer: Function
    }
  },
  methods: {
    async fetchParagraph() {
      const response = await fetch(`https://baconipsum.com/api/?type=meat-and-filler&paras=${this.paragraph_count}&format=text`);
      const data = await response.text();
      if (data) {
        this.paragraph = data;
      }
    },

    resetTimer() {
      this.second = 60;
      clearInterval(this.timer);
    },

    startTimer() {
      this.timer = setInterval(this.decrementTimer, 1000);
    },

    decrementTimer() {
        if (this.second > 0)
            this.second--;
        else {
          document.getElementById('myModal').style.display = 'block';
        }
    }
  },
  created() {
    this.fetchParagraph();
  }
}
</script>

